* {
  color: white;
  /*border: 1px solid red;*/
  border: none;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", "Verdana", sans-serif;
  margin: 0;
  padding: 96px 0 0 0; /* body starts after 93px, so after navbar which is 93px tall */
  background-color: black;

  /* padding-top: 96px; /* makes top of page content not go under fixed navbar */
}

p {
  margin: 0;
  padding: 0;
  font-size: 20px;
}

form, form input {
  font-size: 20px;
  color: lightgrey;
}

a, button, span {
  font-size: 20px;
  cursor: pointer;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {   /* h7 didn't seem to exist, so that's every 'h' */
  font-family: "Orbitron", sans-serif;
}

/* ----------- top navigation bar: -------------- */

@media only screen and (max-width: 907px) {
  .navbar ul {
    display: none;
  }
}

.navbar {
  overflow: hidden;
  background-color: black;
  top: 0;
  width: 100%;
  position: absolute;
  z-index: 50;
}

ul {
  float: right;
  margin: 0 114px 0 0;
  list-style-type: none;
}

li {
  float: left;
}

li a {
  display: block;
  padding: 40px 16px;
  font-size: 13px;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s;
  /*border-radius: 2rem 0 0 0;*/
}

li a:hover:not(.active) {
  color: #0048ce;
}

.active {
  color: #0048ce;
}

/* --------- search bar (onclick 'dropdown'): ---------- */

#searchIcon {
  position: absolute;
  top: 37px;
  right: 80px;
  font-size: 18px;
}
#searchIcon:hover {
  cursor: pointer;
}

#searchBar {
  z-index: 45;
  overflow: hidden;
  position: absolute;
  width: 100%;
  background-color: rgba(17, 17, 17, .95);
  transition: height 0.5s;
}

#searchBar-center {
  max-width: 500px;
  margin: 25px auto;
}

#search-container {
  padding: 0 5%;
  background-color: transparent;
}

#search-container input[type="text"] {
  border-radius: 10px 0px 0px 10px;
  border-top: 2px solid white;
  border-left: 2px solid white;
  border-bottom: 2px solid white;
  height: 50px;
  padding: 0 3px 0 10px;
  float: left;
  color: white;
  font-size: 16px;
  width: 90%;
  background-color: transparent;
}
#search-container input:focus {
  outline: none;
}

#search-container button {
  border-radius: 0px 10px 10px 0px;
  border-top: 2px solid white;
  border-right: 2px solid white;
  border-bottom: 2px solid white;
  width: 10%;
  float: right;
  padding: 5px 10px 5px 5px;
  font-size: 30px;
  cursor: pointer;
  background-color: transparent;
}
.fa-search {
  color: white;
  transition: color 0.3s;
}
.fa-search:hover {
  color: #0048ce;
}

@media screen and (max-width: 475px) {
  #search-container input[type="text"] {
    height: 33px;
  }
  #search-container button {
    font-size: 16px;
  }
}

/* ------- navbar burger button ------- */

/*  this WAS making burger menu icon FOR MOBILE ONLY:

@media only screen and (min-width: 1031px) {
  .navbar span {
    display: none;
  }
}
*/

.navbar span {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 12px 16px;
  transition: color 0.3s;
  font-size: 26px;
  z-index: 51;
}
.navbar span:hover {
  color: #0048ce;
}

#topMenu {
  position: fixed;
  top: 0;
  /* ignore now: */
  /*margin-top: 93px;*/
  /* scrollFunction 2 in Navbar.js = when scrolling down more than navbar's height, change dropdown menu to 'position: fixed' and 'marginTop: 0', 
  so dropdown menu stays on top of screen, (normally it's under navbar, goes back there when scrolling up) */
  width: 100%;
  z-index: 49;
  background-color: rgba(17, 17, 17, .95);
  transition: padding, height, 0.5s;
  overflow: hidden;
}

#topMenu-center {
  max-width: 500px;
  margin: auto;
}

#topMenu a {
  text-decoration: none;
  padding: 8px 8px 8px 32px;
  transition: color 0.3s;
  color: #818181;
  display: block;
}

#topMenu a:hover {
  color: #f1f1f1;
}

@media screen and (max-height: 460px) {
  #topMenu {
    padding-top: 10px;
  }
  #topMenu a {
    font-size: 18px;
  }
}

#closebtn {
  font-size: 46px;
  margin: 0 0 0 28px;
}
#closebtn:hover {
  cursor: pointer;
}

