/* ------- navbar main title ------- */

#main-title {
  font-size: 20px;
  margin: 21px;
  float: left;
  color: white;
  padding: 12px 20px;
  font-family: "Orbitron", sans-serif;
  border: solid 1px #0048ce;
}

/*---------- icons: ---------*/

.fa-cloud,
.fa-clock,
.fa-file {
  font-size: 50px;
  display: inline-block;
  color: white;
  padding: 20px 20px 0 0;
}

/*-------------------------- text boxes with images: ------------------------------*/

.container-top {
  display: grid;
  border: 4px solid #0048ce;
  gap: 4px;
  background-color: #0048ce;
}
@media only screen and (min-width: 1553px) {
  .container-top {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media only screen and (max-width: 1552px) {
  .container-top {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (max-width: 655px) {
  .container-top {
    grid-template-columns: auto;
  }
}

/*
.container-top-box {
    min-height: 407px;
}
*/

.container-top-box:hover {
  cursor: pointer;
}

.container-top-box-content {
  padding: 14%;
  background-color: rgba(1,1,1,.8);
  transition: background-color 0.6s;
  height: 100%;
}

.container-top-box h2 {
  color: white;
  text-shadow: 2px 2px black;
}

.image-filter {
  background-color: rgba(0, 54, 153, 0.4);
  height: 100%;
}

/* muistutus: .parent:hover .child, .parent:focus .child {}  ->  tarkoittaa: kun parenttia hoveraa, tee jotain childille */

/* ----------------------- .image-div: -------------------------- */

.image-div {
  background-size: 120%;
  background-position: center;
  background-repeat: no-repeat;
  transition: background-size 0.6s;
  height: 100%;
}
@media screen and (min-width: 1553px) and (max-height: 1700px) {
  .image-div {
    background-size: 160%;
  }
}
@media screen and (max-width: 958px) {
  .image-div {
    background-size: 150%;
  }
}
@media screen and (max-width: 814px) {
  .image-div {
    background-size: 180%;
  }
}
@media screen and (max-width: 339px) {
  .image-div {
    background-size: 300%;
  }
}

/* --------- zoom image on hover: ------------ */

.image-div:nth-of-type(1):hover {
  background-size: 140%;
}
@media screen and (min-width: 1553px) and (max-height: 1700px) {
  .image-div:nth-of-type(1):hover {
    background-size: 180%;
  }
}
@media screen and (max-width: 958px) {
  .image-div:nth-of-type(1):hover {
    background-size: 170%;
  }
}
@media screen and (max-width: 814px) {
  .image-div:nth-of-type(1):hover {
    background-size: 200%;
  }
}
@media screen and (max-width: 339px) {
  .image-div:nth-of-type(1):hover {
    background-size: 300%;
  }
}

/* ------------------------------------------------------------------------- */

.container-top:hover .container-top-box-content {background-color: black;}
.container-top-box-content:nth-child(1):hover {background-color: transparent;}

/*----- three grey boxes and background: -----*/

.container-fullwide {
  background-color: black;
  width: 100%;
  margin: 0;
}
@media only screen and (min-width: 1552px) {
  .container-fullwide {
    padding: 50px 0;
  }
}

.container-centered {
  margin: auto;
  max-width: 1200px;
}

.image-filter-2 {
  background: url(./images/background1.jpg) no-repeat fixed center;
}

.container-grid {
  display: grid;
}
@media only screen and (min-width: 1552px) {
  .container-grid {
    grid-template-columns: auto auto;
  }
  .bottombox {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}
@media only screen and (max-width: 1552px) {
  .container-grid {
    grid-template-columns: auto;
  }
}

.leftbox {
  background-color: rgba(41, 42, 48, 0.8);
}
.rightbox {
  background-color: rgba(64, 64, 73, 0.8);
}
.bottombox {
  background-color: rgba(25, 27, 31, 0.8);
}
.leftbox,
.bottombox,
.rightbox {
  padding: 45px;
}
@media only screen and (min-width: 1552px) {
  .leftbox,
  .bottombox,
  .rightbox {
    min-height: 350px;
  }
}

.leftbox h2,
.rightbox h2,
.bottombox h2 {
  display: inline;
  color: white;
}

.leftbox p,
.rightbox p,
.bottombox p {
  padding: 25px 0 25px 22px;
  color: white;
}

/* video in background with content over it */

.videobox-container {
  height: 450px;
  position: relative;
  display: grid;
  place-items: center; /* works with grid */
  width: 100%;
  margin: 0 auto;
}

video.fullscreen {
  position: absolute;
  z-index: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  /*
    &::-webkit-media-controls {
      display:none !important;
    }
    */
}

.videobox-content {
  z-index: 1;
  text-align: center;
  max-width: 520px;
}
.videobox-content h2 {
  font-size: 40px;
  margin-bottom: 15px;
  text-align: center;
}

.videobox-gradient {
  position: absolute;
  background-image: linear-gradient(black, transparent);
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* ---------form on front page: ---------*/

#form-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
#form-container-image {
  background: url(./images/background1-dark.png) no-repeat bottom;
  box-shadow: inset -100px 0px 100px 0 black;
}

#form-div {
  margin: auto;
  max-width: 800px;
}
#form-container-form form input {
  padding: 0 0 5px 0;
  margin: 15px 0 30px 0;
  border-bottom: 3px solid grey;
  background-color: transparent;
  width: 100%;
}

@media only screen and (max-width: 710px) {
  #form-container {
    grid-template-columns: 1fr;
  }
  #form-container-form {
    margin: 100px 7% 100px 7%;
  }
}
@media only screen and (min-width: 711px) and (max-width: 1349px) {
  #form-container-form {
    margin: 100px 7% 100px 0;
  }
}
@media only screen and (min-width: 1350px) {
  #form-container-form {
    margin: 100px;
  }
}

/* ---------footer and content: ---------*/

footer {
  /* border-top: 6px solid #0048ce; */
  width: 100%;
  background-color: rgb(17, 17, 17);
  min-height: 300px;
}
.footer-content {
  padding-top: 90px;
  text-align: center;
  max-width: 500px;
  margin: auto;
}

.hr-horizontal {
  width: 100%;
  height: 2px;
  border: none;
  background-image: linear-gradient(to right,rgba(0, 0, 0, 0),white,rgba(0, 0, 0, 0));
  margin: 0 0 20px 0;
}

/* <hr> vertical ----------------------------------------------------------------------------*/

.hr-vertical-container {
  float: left;
}
.hr-vertical {
  float: left;
  width: 2px;
  height: 150px;
  background-image: linear-gradient(rgba(0, 0, 0, 0),white,rgba(0, 0, 0, 0));
}
.hr-vertical-shadow { /* the 'shadow' you see right of the 'line' in dropdown menu */
  /*background-image: linear-gradient(transparent,white, transparent);*/
  /*background-image: linear-gradient(to right, transparent, transparent, white, rgba(255,255,255,.8), rgba(255,255,255,.4), transparent);xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx
  filter: blur(10px);*/
  float: left;
  margin-top: 27px;
  height: 110px;
  width: 18px;
}
/*-------------------------------------------------------------------------------------------*/

footer a {
  color: #0048ce;
  text-decoration: none;
}

/* button takes to top of page, rest of code in .JS file */

#scrollToTopBtn {
  background-color: transparent;
  font-size: 44px;
  font-family: "Advent Pro";
  position: fixed;
  right: 50px;
  bottom: 0px;
  opacity: 0;
  border: 2px solid white;
  border-radius: 3px;
  transition: opacity, bottom, 0.5s linear;
  padding: 13px;
  height: 47px;
  color: white;
}

/* ------buttons for boxes ------*/

.button-align {
  margin-top: 30px;
  display: grid;
}
.button-container {
  margin: auto;
  height: 67px;
}

.button-hover-filter {
  height: 0%;
  background-color: #0048ce;
  /*background-color:rgb(70, 70, 80);*/
  transition: height 0.3s;
}

.button-container:hover .button-hover-filter {
  height: 100%;
}

.button-link {
  background-color: transparent;
  border: 2px solid #0048ce;
  border-radius: 3px;
  padding: 20px;
}

/* media queries for small screens: */

@media only screen and (max-width: 800px) {
  .bar-content {
    max-width: 600px;
    padding: 30px;
  }

  #profile_picture img {
    width: 100px;
    height: 100px;
  }

  h1 {
    font-size: 25px;
  }

  a.some_icon,
  .some_icon img {
    float: right;
    height: 25px;
  }

  .linkedin-link {
    display: none;
  }
}

@media only screen and (max-width: 579px) {
  hr {
    display: none;
  }
}

@media only screen and (max-width: 455px) {
  #profile_picture img {
    margin-bottom: 50px;
  }
}